import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';

Vue.use(VueAxios, axios);


let postService = Vue.axios.create({
    method: 'POST',
    baseURL: '/api/'
})

let apiPostBaseUrl = Vue.axios.create({
    method: 'POST',
    baseURL: '/api/open/AdvertisementService/'
})
let apiGetBaseUrl = Vue.axios.create({
    method: 'POST',
    baseURL: '/api/open/AdvertisementService/'
})

export default {
    getShowAdList: () => {
        return apiPostBaseUrl({ url: 'getShowAdList' })
    },
    addAdClick: (opts) => {
        return apiPostBaseUrl({ url: 'addAdClick', data: { ...opts } })
    }

}